import React, { Component } from 'react';
import { Link,Redirect } from 'react-router-dom';
import { Button, Card, CardBody, CardGroup, Col, Container, Form, Input, InputGroup, InputGroupAddon, InputGroupText, Row, Alert } from 'reactstrap';
import Cookies from 'universal-cookie';
const cookies = new Cookies();

class Login extends Component {
  constructor(props) {
    super(props);
    this.updateInputValue = this.updateInputValue.bind(this);
    this.requestLogin = this.requestLogin.bind(this);
    this.state = {
      username: '',
      password: '',
      loggedIn: false,
      verified: false,
      notverified: false,
    };
  }

  componentDidMount(){
    console.log(this.props.location.search);
    if(this.props.location.search === '?verified=true'){
      this.setState({
        verified: true
      });
    }

    if(this.props.location.search === '?verified=false'){
      this.setState({
        notverified: true
      });
    }
    
    if(cookies.get('token') !== undefined && cookies.get('token') !== null){
      this.setState({
        loggedIn: true
      });
    }
  }

  requestLogin(){
    const requestOptions = {
      method: 'POST',
      headers: { 'Content-Type': 'application/json' },
      body: JSON.stringify({ email: this.state.username, password: this.state.password })
    };
    fetch('https://missionaustralia-back.herokuapp.com/login', requestOptions)
    .then(response =>  response.json())
    .then(resData => {
       console.log(resData)
       if(resData.data.status === 200){
        alert("Login successful");
        this.setState({
          loggedIn: true
        });
        cookies.set('token', resData.data.token, { path: '/' });
        
        if(resData.data.admin === true){
          cookies.set('admin', resData.data.title, { path: '/' });
        } else {
          cookies.set('admin', false, { path: '/' });
        }
       } else {
         alert(resData.data.message);
       }
    });
  }

  updateInputValue(evt,val){
    if(val === 1){
      this.setState({
        username: evt.target.value
      }, () => {
        console.log("Username:", this.state.username);
      });
    } else if(val === 2){
      this.setState({
        password: evt.target.value
      }, () => {
        console.log("Password:", this.state.password);
      })
    }
  }

  render() {
    if(!this.state.loggedIn){
    return (
      <div className="app flex-row align-items-center">
        <Container>
          <Row className="justify-content-center">
            <Col md="8">
              <CardGroup>
                <Card className="p-4">
                { this.state.verified === true?
                  <Alert color="success">
                    Email Verified
                  </Alert>
                  : null
                }

                { this.state.notverified === true?
                  <Alert color="danger">
                    Email verification failed
                  </Alert>
                  : null
                }
                
                  <CardBody>
                    <Form>
                      <h1>Login</h1>
                      <p className="text-muted">Sign In to your account</p>
                      <InputGroup className="mb-3">
                        <InputGroupAddon addonType="prepend">
                          <InputGroupText>
                            @
                          </InputGroupText>
                        </InputGroupAddon>
                        <Input type="text" placeholder="Email" autoComplete="username" id="username" value={this.state.username}  onChange={ evt => this.updateInputValue(evt,1) }/>
                      </InputGroup>
                      <InputGroup className="mb-4">
                        <InputGroupAddon addonType="prepend">
                          <InputGroupText>
                            <i className="icon-lock"></i>
                          </InputGroupText>
                        </InputGroupAddon>
                        <Input type="password" placeholder="Password" autoComplete="current-password" id="password" value={this.state.password}  onChange={ evt => this.updateInputValue(evt,2) } />
                      </InputGroup>
                      <a href="/#/forgot">Forgot password</a>
                      <Row>
                        <Col xs="6">
                          <Button color="primary" className="px-4" type="button" onClick={this.requestLogin}>Login</Button>
                        </Col>
                        {/* <Col xs="6" className="text-right">
                          <Button color="link" className="px-0">Forgot password?</Button>
                        </Col> */}
                      </Row>
                    </Form>
                  </CardBody>
                </Card>
                <Card className="text-white bg-primary py-5 d-md-down-none" style={{ width: '44%' }}>
                  <CardBody className="text-center">
                    <div>
                      <h2>Sign up</h2>
                      {/* <p>Lorem ipsum dolor sit amet, consectetur adipisicing elit, sed do eiusmod tempor incididunt ut
                        labore et dolore magna aliqua.</p> */}
                      <Link to="/register">
                        <Button color="primary" className="mt-3" active tabIndex={-1}>Register Now!</Button>
                      </Link>
                    </div>
                  </CardBody>
                </Card>
              </CardGroup>
            </Col>
          </Row>
        </Container>
      </div>
    );
    } else {
      return <Redirect to='/dashboard'/>
    }
  }
}

export default Login;
